}
},
methods : {
showTab (ev){
if(ev.target.title == ''){
this.showTab1 = true,
this.showTab2 = false,
this.showTab3 = false
}else if(ev.target.title == ''){
this.showTab2 = true,
this.showTab1 = false,
this.showTab3 = false
}else{
this.showTab3 = true,
this.showTab2 = false,
this.showTab1 = false
}
},
shake (ev){
ev.target.classList.add('shake');
setTimeout(function (){
ev.target.classList.remove('shake');
},950)
},
zoom (ev){

ev.target.classList.add('active');

setTimeout(function (){
ev.target.classList.remove('active');
},600);

}
}
